import PropTypes from 'prop-types';

// @mui
import { Box, Card, Grid } from '@mui/material';

// project
import PageContainer from 'layouts/PageContainer';
import PageHeader from 'layouts/PageHeader';
import { useTheme } from '@emotion/react';
import MainCard from 'components/cards/MainCard';

// ----------------------------------------------------------------=------------------

const ShadowBox = ({ shadow }) => {
  const theme = useTheme();

  return (
    <Card sx={{ mb: 3, boxShadow: shadow }}>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          py: 2,
          bgcolor: theme.palette.mode === 'dark' ? 'grey.800' : 'grey.100'
          // color: theme.palette.mode === 'light' ? 'grey.800' : 'grey.200',
        }}
      >
        <Box sx={{ color: 'inherit' }}>{shadow}</Box>
      </Box>
    </Card>
  );
};

ShadowBox.propTypes = {
  shadow: PropTypes.number.isRequired
};

// -------------------------------------------------------------------------------

const UtilitiesShadow = () => {
  return (
    <PageContainer>
      <PageHeader
        title="Shadows"
        subtitle="Shadows allow you to control relative depth, or distance, between two surfaces along the z-axis. "
      />
      <Box mt={6} />
      <MainCard>
        <Grid container columnSpacing={3}>
          {[...Array(25)].map((_, index) => (
            <Grid item key={index} xs={12} sm={4} md={3} lg={3}>
              <ShadowBox shadow={index} />
            </Grid>
          ))}
        </Grid>
      </MainCard>
    </PageContainer>
  );
};

export default UtilitiesShadow;
